/* Base Reset and Font Setup */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  width: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  background-color: #fff;
}

/* Prevent horizontal scroll everywhere */
section, header, footer, div, img {
  max-width: 100%;
  overflow-x: hidden;
}
section {
  scroll-margin-top: 80px;
}
/* Optional: Add default styling for anchor tags */
a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

/* Optional: Fix for images not scaling properly */
img {
  display: block;
  max-width: 100%;
  height: auto;
}

/* Optional: Ensure navbar or fixed headers don't overlap */
header {
  position: relative;
  z-index: 999;
}
